.editor {
  font-size: 52px;
  line-height: 1.5em;
  padding-top: 15vmin;
  max-width: 100vw;
  min-height: 100vh;
  overflow-x: hidden;
}

@media screen and (max-width: 1200px) {
  .editor {
    font-size: 36px;
  }
}

@media screen and (max-width: 600px) {
  .editor{
    font-size: 22px;
  }
}


.line {
  width: 100%;
  display: grid;
  grid-template-columns: 10vw 1fr;
  gap: 1em;
}

.line p:hover {
  background: var(--nvim-gray);
}

.line p {
  padding-right: 1em;
  margin: 0;
}

.line .number {
  min-width: 1.5em;
  text-align: right;
  font-size: 0.9em;
  color: var(--nvim-gray);
}

.file {
  padding-left: 1em;
  line-break: anywhere;
}

.file::before{
  content: "~";
  position: relative;
  top: 0.1em;
  right: 0.5em;
  color: var(--nvim-blue); 
}

.folder {
  color: var(--nvim-blue);
}

.file:hover {
  cursor: pointer;
}

a {
  text-decoration: none;
  text-decoration-line: none;
  color: var(--nvim-text);
}
